.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #F9F9F9;
  font-weight: 700;

  &__top {
    margin-bottom: 32px;
  }

  @include on-tablet {
    margin-bottom: 0;
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 23px;
    color: inherit;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 4px;
    border-bottom: 1px solid $main-text-color;
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}

