.page {
  font-family: Manrope, sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: $main-text-color;

  scroll-behavior: smooth;

   &:has(.page__menu:target) {
    overflow: hidden;
   }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transition: all 0.3s;
  transform: translateX(-100%);
  pointer-events: none;
  padding-left: 20px;
  padding-right: 20px;

  &:target {
    opacity: 1;
    transform: translateX(0);
    pointer-events: all;
    }
  }
}

